.feedback_button{
    height: 35px;
    background-color: white; 
    color: black;
    padding: 0 20px;
    border: 2px solid black;
    border-radius: 8px;
    
}
.feedback_button:hover{
    background-color: black;
    color: white;
    border: 2px solid rgb(204, 204, 204);
}

.feedback_button:disabled{
    border: 2px solid rgb(204, 204, 204);
    background-color: rgb(204, 204, 204); 
    color: black;
}
